.myModal {
    position:  fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0, 0, 0, 0.4);
}
.myModalContent{
    padding: 25px;
    background-color: rgb(149, 150, 151);
    border-radius:  16px;
    min-width: 250px;
}


.myModal.active{
    display: flex;
    justify-content: center;
    align-items: center;
}